
.box{
	max-width: 198px;
	border: 1px solid #c4c4c4;
	margin-bottom: 10px;
	padding: 16px;
	.header{
		margin: 0 -16px;
		padding: 0 16px 16px 16px;
		border-bottom: 1px solid #e5e5e5;
		font-family: 'corbel', Arial, sans-serif;
		color: #161616;
		font-size: 16px;
		text-transform: uppercase;
		font-weight: 400;
	}
	.content{
		margin-top: 10px;
	}
	ul{
		li{
			padding: 10px 0;
			position: relative;
			&:after{
				position: absolute;
				content: '';
				bottom: 0;
				height: 1px;
				border-bottom: 1px solid #c4c4c4;
				max-width: 142px;
				width: 100%;
			}
			a{
				display: block;
				font-family: 'corbel', Arial, sans-serif;
				font-size: 14px;
				color: #161616;
				transition: color 0.2s linear;
			}
			a:hover{
				color: #e74c3c;
			}
		}
		li:last-child{
			&:after{
				border-bottom: none;
			}
		}
	}
	.searchform{
		> div{
			padding-top: 15px;
		}
		input{
			font-size: 16px;
			height: 40px;
			box-sizing: border-box;
			border: 1px solid #ddd;
			-webkit-border-radius: 2px;
			-moz-border-radius: 2px;
			border-radius: 2px;
			font-size: 14px;
			font-weight: 400;
			color: #000;
			// height: 28px;
			line-height: 29px;
			max-width: 100%!important;
			padding: 0 14px;
		}
		label{
			display: none;
		}
		input#searchsubmit{
			visibility: hidden;
			position: absolute;
			left: 0;

		}
	}
}
@media screen and (max-width: 1125px){
	.box{
		margin-bottom: 10px;
		ul{
			li{
				padding: 7px 0;
			}
		}
	}
}
@media (max-width: 767px){
	.box{
		margin-bottom: 27px;
		max-width: 100%;
		width: 48%;
		margin-right: 2%;
		float: left;
		ul{
			li:after{
				max-width: 100%;
			}
		}
	}
	.box:nth-child(2n){
		margin-right: 0%;
		margin-left: 2%;
	}
	.box:nth-child(2n+1){
		clear: left;
	}
}
@media (max-width: 620px){
	.box{
		float: none !important;
		width: 100%;
		max-width: 100%;
		margin-left: auto!important;
		margin-right: auto!important;
		
	}
	.box:nth-child(2n+1){
		clear: both ; 
	}
}