.main_slider{
	.slider__item{
		position: relative;
		cursor: pointer;
		z-index: 1;
		&:before{
			content: "";
			position: absolute;
			display: block;
			background-position: center;
			background-size: cover;
			top: 0;
			height: 100%;
			width: 100%;
		}
	}
	.first_img{
		&:before{
			background-image: url("../img/main_slider1.png");
		}
	}
	.first_img2{
		&:before{
			background-image: url("../img/main_slider2.png");
		}
		.item__contact{
			padding-top: 188px;
		}
	}
	.item{
		&__inner{
			position: relative;
			z-index: 2;
			padding-top: 26px;
			padding-bottom: 60px;
		}
		&__title{
			max-width: 325px;
			margin-bottom: 135px;
			position: relative;
			border-left: 10px solid #d2a26f;
			padding-left: 42px;
			transition: all .3s ease-out;
			// transform: translateX(-10%);
			// opacity: 0;
			filter: alpha(opacity=0);
			.h2{
				color: #7d7d7d;
				font-family: 'HelveticaNeue-Medium', Arial, sans-serif;
				font-weight: normal;
				letter-spacing: -2px;
			}
			&:before{
				position: absolute;
				content: '';
				width: 0;
				height: 0;
				left: 12px;
				top: 7px;
				border-style: solid;
				border-width: 19.5px 0 19.5px 18px;
				border-color: transparent transparent transparent #ffffff;
			}
			.subtitle{
				text-transform: uppercase;
				padding-top: 15px;
				max-width: 115px;
				display: block;
				font-size: 12px;
				font-family: 'HelveticaNeue-Medium', Arial, sans-serif;
			}
		}
		&__contact{
			padding-top: 135px;
			.phone{
				font-family: 'HelveticaNeue-Medium', Arial, sans-serif;
				font-size: 23px;
				color: #7d7d7d;
				padding-bottom: 25px;
				letter-spacing: 0.5px;
				position: relative;
				&:before{
					content: '';
					display: block;
					position: absolute;
					top: -35px;
					left: -4px;
					height: 32px;
					width: 21px;
					background-image: url(../img/pngsprite/sprite.png);
					background-position: -311px -57px;
					background-size: 170px;
				}
			}
		}
	}
	// .slick-slide{
	// 	transition: opacity 1500ms ease !important;
	// }
	// .slick-list{
	// 	height: 100%;
	// }
